* {
    padding: 0;
    margin: 0;
    border: none;
}

body {
    font-size: 16px; /* Increased from 14px to 16px */
    font-family: "Roboto", "sans-serif";
}

.wrapper {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh; /* Increased from 70vh to 80vh */
}

.login-wrapper {
    width: 500px; /* Increased from 400px to 500px */
    height: 450px; /* Increased from 350px to 450px */
    padding: 20px; /* Added padding for better spacing */
    box-sizing: border-box;
}

.login-wrapper > h2 {
    text-align: center;
    font-size: 28px; /* Increased from 24px to 28px */
    color: black;
    margin-bottom: 30px; /* Increased from 20px to 30px */
}

#login-form > input {
    width: 100%;
    height: 58px; /* Increased from 48px to 58px */
    background-color: #f8f8f8;
    padding: 0 15px; /* Increased padding from 10px to 15px */
    box-sizing: border-box;
    margin-bottom: 20px; /* Increased from 16px to 20px */
    border-radius: 8px; /* Increased from 6px to 8px */
}

#login-form > input::placeholder {
    color: #d2d2d2;
    font-size: 18px; /* Increased placeholder font size */
}

#login-form > input[type="submit"] {
    color: #fff;
    background-color: black;
    font-size: 20px; /* Increased from 16px to 20px */
    margin-top: 30px; /* Increased from 20px to 30px */
    height: 60px; /* Adjusted height for consistency */
}

#login-form > input[type="checkbox"] {
    display: none;
}

#login-form > label {
    color: #999999;
    font-size: 18px; /* Increased label font size */
}
