.newsname {
    width: 100%;
    text-align: center;
    font-size: 32px; 
    font-weight: bold; 
    margin-top: 40px;
    margin-bottom: 20px;
}

.newsBox {
    margin-left: 300px;
    margin-right: 300px;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 10px;
    display: flex;
    flex-wrap: wrap;
}

.newsBox1 {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.news, .news2 {
    width: 81%;
    border-bottom: 1px solid #ccc;
    padding: 10px;
    text-align: center;
}

.date, .date2 {
    width: 15%;
    padding: 10px;
    border-bottom: 1px solid #ccc;
    border-left: 1px solid #ccc;
    text-align: center;
}

.news, .date{
    font-weight: bold;
    font-size: 20px;
}

.news3{
    width: 81%;
    padding: 10px;
    text-align: center;
}

.date3{
    width: 15%;
    padding: 10px;
    border-left: 1px solid #ccc;
    text-align: center;
}